.project-card {
  position: relative;
  overflow: hidden;
  background: #2f81ed3f;
  background: linear-gradient(to right, #56cbf23f, #2f81ed3f);
  box-shadow: 1px 2px 17px 0px rgba(0, 0, 0, 0.18);
  -webkit-box-shadow: 1px 2px 17px 0px rgba(0, 0, 0, 0.18);
  -moz-box-shadow: 1px 2px 17px 0px rgba(0, 0, 0, 0.18);
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  overflow: hidden;
  transition: all 300ms ease-out;
}

.project-card:hover {
  box-shadow: 0px 6px 13px -2px rgba(0, 0, 0, 0.2);
  transform: scale(1.02);
}

.project-card:hover .img-section img .imageCardEffect::before {
  opacity: 0.8;
  transform: scale(1);
}
.project-card:hover .imageCardEffect::before {
  transform: rotate(45deg) scale(2.5) translate(0px, 100%);
}

.img-section {
  position: relative;
  overflow: hidden;
}

.img-section img {
  position: relative;
  display: block;
  height: 14rem;
  width: 100%;
  object-fit: cover;
  transition: opacity 0.35s, transform 0.35s;
  transform: scale(1.15);
}

.img-section .imageCardEffect {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 30px 10px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  text-indent: 9999px;
}


.project-info {
  padding: 1rem;
}

.color-white {
  color: white;
}

.project-card-btn {
  position: relative;
  background: linear-gradient(to right, #56ccf2, #2f80ed);
  padding: 5px 10px;
  border-radius: 5px;
  color: white;
  text-decoration: none !important;
  cursor: pointer;
}
.project-card-btn::before,
.project-card-btn::after {
  content: "";
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: linear-gradient(to right, #56ccf2, #2f80ed);
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}


